@import "styles/color.scss";

.root {
  .first-container {
    display: flex;
    flex-direction: column;
    padding: 10px;
    margin-bottom: 20px;

    .groups {
      display: flex;
      flex-direction: row;

      .input {
        width: 25%;
        padding: 10px;
      }
    }

    .child-container {
      display: flex;
    }
  }

  .tables-container {
    display: flex;

    .left-side {
      display: flex;
      flex-direction: column;
      width: 600px;

      .wrapper {
        display: flex;
        flex-direction: column;
        padding: 20px;
        margin-bottom: 20px;

        .table {
          margin-bottom: 20px;
          flex: 1;
        }

        .table-header {
          display: flex;
          padding-bottom: 10px;
          .table-description {
            flex: 1;
            color: $color-1;
          }

          .add-link {
            font-weight: 500;
            text-decoration: underline;
            cursor: pointer;
          }
        }

        .row {
          .align {
            &.center {
              text-align: center;
            }

            &.right {
              text-align: right;
            }
          }
        }
        .table-total {
          display: flex;
          padding-top: 10px;
          width: 100%;
          .caption {
            flex: 1;
            display: flex;
            justify-content: flex-end;
            padding-right: 20px;

            font-weight: bold;
          }

          .amount {
            font-weight: bold;
          }
        }
      }
    }

    .right-side {
      flex: 1;
      padding-left: 20px;

      .wrapper {
        padding: 20px;

        .groups {
          display: flex;
          flex-direction: row;
          padding-top: 10px;

          .input {
            flex: 1;
            padding: 10px;

            &:first-child {
              padding-left: unset;
            }

            &:last-child {
              padding-right: unset;
            }
          }
        }
      }
    }
  }
}
